import { request } from '../../helpers/newClientApi'
import { router } from './../../main'
import { omit } from 'ramda'

const state = {
  locations: [],
  isMoreLocationAvialiable: true,
  pagination: {
    limit: 10,
    page: 1,
    count: 0
  },
  searchResult: [],
  searchValue: '',
  location: {}
}

const getters = {
  getlocations: () => state.locations,
  isMoreLocationAvialiable: () => state.isMoreLocationAvialiable,
  searchResult: () => state.searchResult
}

const mutations = {
  setLocation(sub, { location }) {
    state.location = {
      ...location,
      hours: location.hours ? JSON.parse(location.hours) : [],
      category: location.category_name,
      formerLocation: {
        lat: location.lat,
        long: location.long
      }
    }
  },
  setLocations(sub, { locations, count, page }) {
    if (state.pagination.offset > 0) {
      state.locations = [
        ...state.locations,
        ...locations
      ]
    } else {
      state.locations = locations
    }
    state.pagination = {
      ...state.pagination,
      count,
      page
    }
  },
  updateLocation(sub, { location, _id }) {
    const p = state.locations.indexOf(item => item._id === _id)
    state.locations[p] = { ...location }
  },
  setLocationPagination(sub, { limit, newOffset }) {
    state.pagination = {
      limit,
      offset: newOffset
    }
  },
  updateLocationForm(sub, payload) {
    state.location[payload.key] = payload.value
  },
  stopLocationLazzyLoad() {
    state.isMoreLocationAvialiable = false
  },
  removeLocation(sub, id) {
    state.locations = state.locations.filter(item => item._id !== id)
  },
  setSearchResult(sub, data) {
    state.searchResult = data
  },
  saveSearchValue(sub, s) {
    state.searchValue = s
  },
  addLocation(sub, location) {
    state.locations.push(location)
  },
  clearPlaceState(sub) {
    state.location = {}
  }
}

const actions = {
  showAlert({ commit }, msg) {
    const payload = {
      type: 'success',
      msg,
      auto: true
    }
    commit('SET_ALERT', payload)
    commit('DISPLAY_ALERT')
    if (payload.auto) {
      setTimeout(() => {
        commit('DISPLAY_ALERT')
      }, 2000)
    }
  },
  async createLocation({ commit, dispatch, rootState: { locations } = [] }) {
    try {
      const { location, location: { category_id, hours } } = locations
      const locationCreated = {
        ...location,
        category: category_id.title,
        hours: JSON.stringify(hours)
      }
      await request('post', 'locations', locationCreated)
      router.push('/locations/list')
      await commit('addLocation', { locationCreated })
      await dispatch('showAlert', 'Location created')
    } catch (err) {
      console.log(err)
    }
  },
  async updateLocation({ commit, dispatch }) {
    const {
      location,
      location: {
        _id,
        hours
      }
    } = state
    const payload = {
      ...omit(['createdAt', 'updatedAt'], location),
      hours: JSON.stringify(hours)
    }
    try {
      await request(
        'put',
        `locations/${_id}`,
        { ...payload }
      )
      router.push('/locations/list')
      await commit('updateLocation', { location, _id })
      await dispatch('showAlert', 'Location was updated')
    } catch (err) {
      throw err
    }
  },
  async removeLocation({ commit, dispatch }, { id }) {
    try {
      await request(
        'delete',
        `locations/${id}`
      )
      commit('removeLocation', id)
      dispatch('showAlert', 'Place deleted')
    } catch (err) {
      throw err
    }
  },
  showMoreLocations({ commit, rootState, dispatch }) {
    dispatch('fetchLocations')
  },
  async fetchLocations({ commit, rootState }) {
    try {
      const { pagination: { page, limit } } = state
      const {
        docs,
        nextPage,
        hasNextPage,
        totalDocs
      } = await request('get', `locations?limit=${limit}&offset=${page}`)
      if (!hasNextPage) {
        commit('stopLocationLazzyLoad')
      }
      await commit('setLocations', {
        locations: docs,
        totalDocs,
        page: nextPage
      })
    } catch (err) {
      console.log(err)
    }
  },
  async findLocation({ commit }, s) {
    try {
      const { docs } = await request('get', `locations/search?s=${s}`)
      commit('setSearchResult', docs)
    } catch (err) {
      throw err
    }
  },
  async globalSearch({ commit, dispatch }, s) {
    commit('saveSearchValue', s)
    if (s.length === 0) {
      return dispatch('fetchLocations')
    }
    const { docs } = await request('get', `locations/search?s=${s}`)
    await commit('setLocations', { locations: docs, count: 0 })
  },
  async fetchLocation({ commit }, { id }) {
    try {
      const location = await request('get', `locations/${id}`)
      await commit('setLocation', { location })
    } catch (err) {
      throw err
    }
  },
  async orderLocations({ dispatch }, items) {
    try {
      items.map(async({ location_id, order }) => {
        await request(
          'put',
          `locations/${location_id}`,
          { order }
        )
      })
      await dispatch(
        'showAlert',
        'Locations was reordered',
        { root: true }
      )
    } catch (err) {
      throw err
    }
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



