
import { stringify } from 'query-string'
import { uniq, omit } from 'ramda'
import { prepareEvent, prepareList } from '../handlers/eventHandler'
import { request } from '../../helpers/newClientApi'

const state = {
  event: {
    poster: '',
    title: '',
    linkedId: 'none',
    location: '',
    timeData: {}
  },
  events: [],
  pagination: {
    limit: 10,
    page: 1
  },
  isMoreAvialiable: true,
  searchValue: ''
}

const getters = {
  getEvents: () => uniq(state.events),
  getEvent: () => state.event,
  isMoreAvialiable: () => state.isMoreAvialiable
}

const mutations = {
  setEvent(sub, event) {
    state.event = {
      ...prepareEvent(event)
    }
  },
  setPoster(sub, poster) {
    state.event.poster = poster
  },
  updateEventForm(sub, payload) {
    state.event[payload.key] = payload.value
  },
  setEvents(sub, { events, count, isSearch = false, page }) {
    if (!isSearch) {
      state.events = [
        ...state.events,
        ...events
      ]
    } else {
      state.events = events
    }
    state.pagination = {
      ...state.pagination,
      count,
      page
    }
  },
  resetEvent() {
    for (let key in state.event) {
      state.event[key] = ''
    }
    state.event.poster = 'none'
  },
  addEvent(sub, event) {
    state.events.push(event)
  },
  updateEvent(sub, event, id) {
    const index = state.events.indexOf(({ _id }) => _id === id)
    state.events[index] = { ...event }
  },
  removeEvent(sub, id) {
    state.events = state.events.filter(
      ({ _id }) => _id !== id
    )
  },
  setPagination(sub, { limit, newOffset }) {
    state.pagination = {
      limit,
      offset: newOffset
    }
  },
  stopLazzyLoad() {
    state.isMoreAvialiable = !state.isMoreAvialiable
  },
  saveEventSearchValue(sub, value) {
    state.searchValue = value
  }
}

const actions = {
  async createEvent({ commit, dispatch }) {
    const {
      event,
      event: {
        location_id,
        category_id,
        days,
        startDate,
        endDate
      }
    } = state
    const newStartDate = startDate instanceof Date ? Date.parse(startDate) : startDate
    const newEndDate = endDate instanceof Date ? Date.parse(endDate) : endDate
    const updatedEvent = {
      ...omit(['createdAt', 'updatedAt'], event),
      category: category_id.title,
      location_name: location_id.title,
      place_id: location_id._id,
      days: stringify(days, { arrayFormat: 'bracket' }),
      startDate: newStartDate,
      endDate: newEndDate
    }

    try {
      await request('post', 'events', updatedEvent)
      await commit('addEvent', updatedEvent)
      await dispatch('showAlert', 'Event created', { root: true })
    } catch (err) {
      throw err
    }
  },
  async updateEvent({ commit, dispatch }, { id }) {
    const { event, event: { location_id, days, startDate, endDate } } = state
    const newStartDate = startDate instanceof Date ? Date.parse(startDate) : startDate
    const newEndDate = endDate instanceof Date ? Date.parse(endDate) : endDate
    const updatedEvent = {
      ...omit(['createdAt', 'updatedAt'], event),
      place_id: location_id._id,
      location_name: location_id.title,
      days: stringify(days, { arrayFormat: 'bracket' }),
      startDate: newStartDate,
      endDate: newEndDate
    }
    try {
      await request('put', `events/${id}`, { ...updatedEvent })
      await dispatch('showAlert', 'Event updated succesfull', { root: true })
      await commit('updateEvent', event, id)
    } catch (err) {
      throw err
    }
  },
  async removeEvent({ dispatch, commit }, { id }) {
    try {
      await request('delete', `events/${id}`)
      await commit('removeEvent', id)
      await dispatch('showAlert', 'Event deleted succesfull', { root: true })
    } catch (err) {
      throw err
    }
  },
  async fetchEvent({ commit }, { id }) {
    try {
      const data = await request('get', `events/${id}`)
      commit('setEvent', data)
    } catch (err) {
      throw err
    }
  },
  showMoreEvents({ commit, rootState, dispatch }) {
    dispatch('fetchEvents')
  },
  async fetchEvents({ commit, rootState }) {
    const { pagination: { limit, page } } = state
    try {
      const { totalDocs, docs, nextPage, hasNextPage } = await request(
        'get',
        `events?limit=${limit}&offset=${page}`
      )
      if (!hasNextPage) {
        commit('stopLazzyLoad')
      }
      const events = prepareList(docs)
      await commit(
        'setEvents',
        {
          events,
          totalDocs,
          nextPage,
          page: nextPage
        }
      )
    } catch (err) {
      throw err
    }
  },
  async globalEventSearch({ commit, dispatch }, s) {
    commit('saveEventSearchValue', s)
    if (s.length === 0) {
      return dispatch('fetchEvents')
    }
    try {
      const data = await request('get', `events/globalSearch?s=${s}`)
      const events = prepareList(data)
      await commit(
        'setEvents',
        {
          events,
          count: 0,
          isSearch: true
        }
      )
    } catch (err) {
      throw err
    }
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



